import React from "react";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "../styles/Carrusel.css";
import Img1 from "../assets/images/Carrusel/Rambla.jpg";
import Img2 from "../assets/images/Carrusel/Cabo Corrientes.png";
import Img3 from "../assets/images/Carrusel/Hotel Bristol.jpg";
import Img4 from "../assets/images/Carrusel/Plaza San Martin.jpg";
import Img5 from "../assets/images/Carrusel/Hotel Bristol.jpeg";

function Gallery() {
  return (
    <div style={{ maxHeight: "500px", maxWidth: "750px", margin: "auto" }}>
      <Carousel
        showArrows={true}
        autoPlay={true}
        infiniteLoop={true}
        showThumbs={false}
        showStatus={false}
        interval={3000}
        renderArrowPrev={(onClickHandler, hasPrev, label) =>
          hasPrev && (
            <button
              type="button"
              onClick={onClickHandler}
              className="custom-arrow custom-arrow-prev"
            >
              &#8592;
            </button>
          )
        }
        renderArrowNext={(onClickHandler, hasNext, label) =>
          hasNext && (
            <button
              type="button"
              onClick={onClickHandler}
              className="custom-arrow custom-arrow-next"
            >
              &#8594;
            </button>
          )
        }
      >
        <div>
          <img src={Img1} alt="Rambla" />
        </div>
        <div>
          <img src={Img2} alt="Cabo Corrientes" />
        </div>
        <div>
          <img src={Img3} alt="Bristol 1905" />
        </div>
        <div>
          <img src={Img4} alt="Plaza San Martin" />
        </div>
        <div>
          <img src={Img5} alt="Hotel Bristol" />
        </div>
      </Carousel>
    </div>
  );
}

export default Gallery;
