import React, { useEffect } from "react";
import { Routes, Route, useLocation } from "react-router-dom";
import Home from "../pages/Home";
import HistoriaCiudadPage from "../pages/HistoriaCiudadPage";
import PersonajesDestacadosPage from "../pages/PersonajesDestacadosPage";
import GaleraPage from "../pages/GaleraPage";
import "../styles/PageTransitions.css";

const AppRoutes = () => {
  const location = useLocation();

  useEffect(() => {
    document.querySelector(".fade-container").classList.add("fade");
    setTimeout(
      () => document.querySelector(".fade-container").classList.remove("fade"),
      500
    );
  }, [location]);

  return (
    <div className="fade-container">
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/historiaciudad" element={<HistoriaCiudadPage />} />
        <Route
          path="/personajesdestacados"
          element={<PersonajesDestacadosPage />}
        />
        <Route path="/galera" element={<GaleraPage />} />
      </Routes>
    </div>
  );
};

export default AppRoutes;
