import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import {
  Navbar as BootstrapNavbar,
  Nav,
  Form,
  FormControl,
  Alert,
  Offcanvas,
} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import Logo from "../assets/icons/Logo.png";
import "../styles/Navbar.css";

const Navbar = () => {
  const [searchQuery, setSearchQuery] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const [showOffcanvas, setShowOffcanvas] = useState(false);
  const navigate = useNavigate();

  const handleSearchSubmit = (event) => {
    event.preventDefault();
    switch (searchQuery.toLowerCase()) {
      case "inicio":
        navigate("/");
        break;
      case "historia de la ciudad":
        navigate("/HistoriaCiudad");
        break;
      case "personajes destacados":
        navigate("/PersonajesDestacados");
        break;
      case "galera":
        navigate("/Galera");
        break;
      default:
        setShowAlert(true);
    }
  };

  const handleNavLinkClick = (path) => {
    navigate(path);
    setShowOffcanvas(false); // Cierra el offcanvas al seleccionar un enlace
  };

  return (
    <div className="navbar-container">
      {showAlert && (
        <Alert variant="danger" onClose={() => setShowAlert(false)} dismissible>
          <Alert.Heading>Oh no! Ha ocurrido un error!</Alert.Heading>
          <p>No se ha encontrado el resultado. Por favor, intenta de nuevo.</p>
        </Alert>
      )}
      <BootstrapNavbar
        bg="transparent"
        variant="dark"
        expand="lg"
        className="justify-content-center"
      >
        <BootstrapNavbar.Brand as={Link} to="/">
          <img
            src={Logo}
            width="75"
            height="75"
            className="d-inline-block align-top rounded-logo logo-margin navbar-logo"
            alt="Logo"
          />
        </BootstrapNavbar.Brand>
        <BootstrapNavbar.Toggle
          aria-controls="offcanvasNavbar"
          onClick={() => setShowOffcanvas(!showOffcanvas)}
        />
        <BootstrapNavbar.Collapse
          id="basic-navbar-nav"
          className="justify-content-center d-none d-lg-flex"
        >
          <Nav>
            <Nav.Link as={Link} to="/" onClick={() => setShowOffcanvas(false)}>
              Inicio
            </Nav.Link>
            <Nav.Link
              as={Link}
              to="/HistoriaCiudad"
              onClick={() => setShowOffcanvas(false)}
            >
              Historia de la Ciudad
            </Nav.Link>
            <Nav.Link
              as={Link}
              to="/PersonajesDestacados"
              onClick={() => setShowOffcanvas(false)}
            >
              Personajes Destacados
            </Nav.Link>
            <Nav.Link
              as={Link}
              to="/Galera"
              onClick={() => setShowOffcanvas(false)}
            >
              Galera
            </Nav.Link>
          </Nav>
        </BootstrapNavbar.Collapse>
        <Form
          inline
          onSubmit={handleSearchSubmit}
          className="search-form justify-content-between"
        >
          <FormControl
            type="text"
            placeholder="Buscar"
            className="mr-sm-2"
            value={searchQuery}
            onChange={(event) => setSearchQuery(event.target.value)}
          />
        </Form>
      </BootstrapNavbar>

      <Offcanvas
        show={showOffcanvas}
        onHide={() => setShowOffcanvas(false)}
        placement="start"
        className="offcanvas-custom"
      >
        <Offcanvas.Header closeButton className="custom-offcanvas-header">
          <Offcanvas.Title>Men</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Nav className="flex-column">
            <Nav.Link as={Link} to="/" onClick={() => handleNavLinkClick("/")}>
              Inicio
            </Nav.Link>
            <Nav.Link
              as={Link}
              to="/HistoriaCiudad"
              onClick={() => handleNavLinkClick("/HistoriaCiudad")}
            >
              Historia de la Ciudad
            </Nav.Link>
            <Nav.Link
              as={Link}
              to="/PersonajesDestacados"
              onClick={() => handleNavLinkClick("/PersonajesDestacados")}
            >
              Personajes Destacados
            </Nav.Link>
            <Nav.Link
              as={Link}
              to="/Galera"
              onClick={() => handleNavLinkClick("/Galera")}
            >
              Galera
            </Nav.Link>
          </Nav>
        </Offcanvas.Body>
      </Offcanvas>
    </div>
  );
};

export default Navbar;
